export default {
  name: 'DialogNormal',
  props: {
    top: {
      default: '15vh',
      type: String
    },
    widthStyle: {
      default: '550px',
      type: String
    },
    title: {
      default: '信息',
      type: String
    },
    subTitle: {
      default: '',
      type: String
    },
    visible: {
      default: false,
      type: Boolean
    },
    showFooter: {
      default: true,
      type: Boolean
    },
    appendToBody: {
      default: false,
      type: Boolean
    }
  },
  data(){
    return {
      dialogNormalVisible: true,
      footer: false,
      header: false
    }
  },
  mounted() {
    this.dialogNormalVisible = this.visible;
    this.footer = this.$slots.footer !== undefined;
    this.header = this.$slots.title !== undefined;
  },
  created() {

  },
  methods: {
    closeDialog(data){
      this.$emit("close", data);
    },
    handleBeforeClose(data){
      this.$emit("before-close", data);
    },
    handleClose(data){
      if (this.footer || this.showFooter == false){
        this.$emit("right-close", data);
      }else {
        this.handleCancel();
      }
    },
    handleOk(data){
      this.$emit("ok", data);
    },
    handleCancel(data){
      this.$emit("cancel", data);
    }
  }
}
