import {oneOf} from '../../../utils/utils';
export default {
  name: 'TerleaveDetail',
  props: {
    selValue: {
      default: function () {
        return {};
      },
      type: Object
    },
    options: {
      default: function () {
        return [];
      },
      type: Array
    }
  },
  computed: {
    selectModel(){
      this.value = this.selValue;
    }
  },
  data() {
    return {
      value: '',
      widthStyle: null,
      widthDesStyle: null
    }
  },
  mounted() {
    this.setWidth();
  },
  created() {

  },
  methods: {
    setWidth(){
      this.$nextTick(function () {
        if (process.client){
          this.widthStyle = document.querySelector(".ellipsis-tag") ? document.querySelector(".ellipsis-tag").clientWidth : 0;
          this.widthDesStyle = document.querySelector(".ellipsis-long-tag") ? document.querySelector(".ellipsis-long-tag").clientWidth : 0;
        }
      });
    }
  }
}
