    initCourseList(day){
      let params = {
        teacherId: this.loginUserId,
        busiTime: day
      };
      this.$axios.get(common.course_static_course_list, {params: params}).then(res => {
        if (res.data.data){
          this.defaultCourseList = res.data.data;
