import {oneOf} from '../../../utils/utils';
export default {
  name: 'AuditButton',
  props: {
    selValue: {
      default: function () {
        return {};
      },
      type: Object
    }
  },
  computed: {
    selectModel(){
      this.value = this.selValue;
    }
  },
  data() {
    return {
      value: '',
      widthStyle: null,
      widthDesStyle: null,
      textarea: '',
      visibleOk: false,
      visibleNo: false
    }
  },
  created() {

  },
  methods: {
    handleOk(data){
      this.$emit('ok', data, this.textarea);
    },
    handleNo(data){
      this.$emit('no', data, this.textarea);
    },
    handleCancel(data){
      this.$emit('cancel', data);
    },
    cancelPop(){
      this.textarea = '';
      this.visibleOk = false;
      this.visibleNo = false;
    }
  }
}
